<!-- 登录页面 -->
<template>
  <div class="log-container">
    <div class="log-container">
      <div class="title">登录</div>
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="电话号码" prop="phone">
          <el-input v-model="ruleForm.userPhone"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')" class="button">登录</el-button>
          <el-button @click="resetForm('ruleForm')" class="button">重置</el-button>
        </el-form-item>
      </el-form>
      <div class="goReg">没有账号？<router-link to="/register">立即注册</router-link></div>
      <div class="problem">
        <div>如果您有任何疑问请咨询我们</div>
        <div>微信：xxxx</div>
        <div>电话：xxxx</div>
        <div>邮箱：xxxx</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Log',
  data() {
    //验证电话号码
    var validatePhone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入电话号码'));
      } else {
        callback()
      }
    }
    //验证密码
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        password: '',
        userPhone: ''
      },
      rules: {
        password: [
          { validator: validatePass, trigger: 'blur' }
        ],
        userPhone: [
          { validator: validatePhone, trigger: 'blur' }
        ]
      },
    };
  },
  methods: {
    async Log() {
      try {
        const {password,userPhone}=this.ruleForm
        const form={password,userPhone}
        this.$store.dispatch('userLogin', {form,vm:this})
      } catch (error) {
        console.log(error);
      }
    },
    //提交表单
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.Log()
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style lang="scss" scoped>
.log-container {
  a {
    color: blue;
  }

  width: 450px;
  height: 550px;
  margin:0 auto;
  font-size: 20px;
  text-align: center;
  border-radius: 10%;
  margin-bottom: 100px;
  overflow: hidden;

  .title {
    height: 100px;
    width: 100px;
    line-height: 100px;
    text-align: center;
    margin: 0 auto;
    margin-top: 40px;
    font-size: 25px;
    font-weight: 600;
  }

  .demo-ruleForm {
    transform: translateX(-30px);

    .button {
      transform: translateX(-15px);
    }
  }

  .goReg {
    font-size: 15px;
  }

  .problem {
    transform: translateX(100px);
    text-align: left;

    div {
      margin: 0;
      height: 25px;
      font-size: 15px;
      transform: translateY(30px);
    }
  }
}</style>