<!-- 头部组件 -->
<template>
  <div class="header">
    <!-- logo -->
    <div class="logo" @click="goHome">
      <div class="log">

      </div>
    </div>
    <!-- 导航栏 -->
    <div class="navigation">
      <div class="nav-home" @mouseenter="funcShow" @mouseleave="funcHide"><span>产品功能</span>
        <div class="downNav" v-if="show2">
          <ul>
            <li @click="goUpload()">简历解析</li>
            <li @click="goPostMatch()">人岗匹配</li>
            <li @click="goDataAnalyze">数据分析</li>
          </ul>
        </div>
      </div>

      <div class="nav-function" @click="goUser"><span>个人中心</span></div>
      <div class="nav-document" @click="goDevelop"><span>开发文档</span></div>
      <div class="nav-about" @click="goAbout"><span>关于我们</span></div>
    </div>
    <!-- 登录及注册 -->
    <div class="log-reg">
      <div class="log" @click="goLog" v-if="!id">登录</div>
      <div class="reg" @click="goReg" v-if="!id">注册</div>
      <div class="user" v-if="id" @mouseover="showBox" style="position: relative; ">
        <el-avatar icon="el-icon-user-solid" class="icon" v-if="!imgUrl"></el-avatar>
        <el-avatar :src="imgUrl" class="icon" v-else></el-avatar>

        <div class="name">用户 &nbsp;{{ userName }} &nbsp;，您好</div>
        <div class="userBigBox" >
          <div class="tri"></div>
          <div class="userBox">
            <el-avatar icon="el-icon-user-solid" class="icons" v-if="!imgUrl"></el-avatar>
            <el-avatar :src="imgUrl" class="icons" v-else></el-avatar>
            <div class="names">{{ userName }}</div>
            <div class="choice">
              <div class="personal" @click="goUserInfo">个人中心</div>
              <div class="logout" @click="logout">退出登录</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Header',
  data() {
    return {
      show: false,
      show2: false
    }
  },
  methods: {
    goUser() {
      this.$router.push({ path: '/userInfo/upload' })
    },
    //触发
    getId(data) {
      this.id = data
    },
    //跳转至数据分析页面
    goDataAnalyze() {
      this.$router.push({ path: '/dataAnalyze' })
    },
    //跳转至关于我们页面
    async goAbout() {
      await this.$router.push({ path: '/about/aboutUs' })
      this.$bus.$emit('index3', 3)
    },
    //跳转至开发文档页面
    async goDevelop() {
      await this.$router.push({ path: '/about/develop' })
      this.$bus.$emit('index1', 1)
    },
    //退出功能
    async logout() {
      await this.$store.dispatch('userLogout')
      this.$router.push('/home')
    },
    //跳转至登录界面
    goLog() {
      this.$router.push({ path: '/log' })
    },
    //跳转至注册界面
    goReg() {
      this.$router.push({ path: '/register' })
    },
    //跳转至首页
    goHome() {
      this.$router.push({ path: '/home' })
    },
    goUpload() {
      this.$router.push({ path: '/analyze' })
    },
    goPostMatch() {
      this.$router.push({ path: '/postMatch' })
    },
    goUserInfo() {
      this.$router.push({ path: '/userInfo' })
    },
    showBox() {
      this.show = !this.show
    },
    hideBox() {
      this.show = false
    },
    funcShow() {
      this.show2 = true
    },
    funcHide() {
      this.show2 = false
    }
  },
  mounted() {
    this.$store.dispatch("getUserInfo", localStorage.getItem('id'))
  },
  computed: {
    id() {
      return this.$store.state.user.id
    },
    userName() {
      return this.$store.state.user.userInfo.userName
    },
    imgUrl() {
      return this.$store.state.user.userInfo.userImg

    }
  }
}
</script>

<style lang="scss" scoped>
.header {
  width: 100%;
  height: 80px;
  position: relative;
  background-color: white;
  display: flex;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.5);

  .logo {
    height: 50px;
    flex: 1;
    transform: translate(10px, 18px);
    font-size: 35px;
    line-height: 85px;
    text-align: center;
    cursor: pointer;
    background-color: rgb(50, 93, 234);
    background: url(../assets/logo.png) no-repeat 100px;
    background-size: 40% 110%;
  }

  .navigation {
    flex: 2;
    display: flex;
    position: relative;

    .nav-home {
      position: relative;

      .downNav {
        position: absolute;
        width: 160px;
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
        background-color: white;
        position: absolute;
        box-shadow: 1px 1px 5px 1px gainsboro;
        z-index: 5;
        cursor: pointer;
        font-size: 14px;

        ul {
          li {
            width: 100%;
            height: 50px;
            font-size: 15px;
            text-align: center;
            line-height: 50px;

            &:hover {
              background-color: #409EFF;

            }
          }
        }

      }
    }

    .nav-home,
    .nav-function,
    .nav-document,
    .nav-about {
      flex: 1;

      span {
        font-size: 15px;
        line-height: 50px;
        text-align: center;
        display: block;
        margin: 20px auto;
        width: 100px;
        height: 80px;
        transition: 0.7s;

        &:hover {
          color: rgb(50, 93, 234);
        }

        cursor: pointer;
      }
    }
  }

  .log-reg {
    flex: 1;
    display: flex;
    justify-content: center;

    .log,
    .reg {
      width: 100px;
      height: 50px;
      margin-top: 20px;
      margin-left: 10px;
      border-radius: 10%;
      font-size: 18px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;

      &:hover {
        opacity: 0.7;
      }
    }

    .log {
      background-color: rgb(23, 70, 224);
      color: white;
    }

    .reg {
      background-color: rgb(225, 231, 252);
      color: rgb(51, 94, 234);
    }

    .user {
      cursor: pointer;
      width: 100px;
      z-index: 5;
      position: relative;

      .userBigBox {
        display: none;
      }
      &:hover {
        .userBigBox {
          display: block;
          
        }
      }

      .icon {
        margin: 25px 30px;
      }

      .name {
        transform: translate(90px, -55px);
      }

      .tri {
        width: 0px;
        height: 0px;
        border: 5px solid #F2F6FC;
        border-bottom-color: #F2F6FC;
        border-top-color: transparent;
        border-left-color: transparent;
        border-right-color: transparent;
        transform: translate(46px, -40px);
      }

      .userBox {
        background-color: white;
        width: 250px;
        height: 200px;
        transform: translate(-70px, -40px);
        box-shadow: 1px 1px 5px black;
        z-index: 10;
        position: fixed;
        .icons {
          transform: scale(1.2);
          margin-left: 105px;
          margin-top: 30px;
          margin-bottom: 10px;
        }

        .names {
          width: 100%;

          text-align: center;
        }

        .choice {
          width: 100%;
          height: 50px;
          transform: translateY(50px);
          background-color: white;
          display: flex;
          justify-content: space-around;
          border-top: 1px solid black;

          .personal,
          .logout {
            text-align: center;
            line-height: 50px;
            cursor: pointer;
          }
        }
      }
    }
  }
}
</style>