<!-- 侧边栏组件 -->
<template>
    <div class="sidebar">
        <el-row class="tac">
            <div class="userImg">
                <el-avatar icon="el-icon-user-solid" class="icon" v-if="!imgUrl"></el-avatar>
                <el-avatar :src="imgUrl" class="icon" v-else></el-avatar>
                <span class="userName">用户 {{ userName }}</span>
                <span class="userCompany">
                    <div v-if="userCompany">{{ userCompany }} </div>
                    <div v-if="!userCompany">未知公司</div>
                </span>
            </div>
            <el-col :span="20" class="col">
                <h5></h5>
                <el-menu :default-active="active" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose"
                    background-color="rgba(27, 42, 78)" text-color="#fff" active-text-color="#ffd04b">
                    <el-menu-item index="3" @click="goUpload">
                        <i class="el-icon-upload"></i>
                        <span slot="title">已上传的简历</span>
                    </el-menu-item>
                    <el-menu-item index="2" @click="goCollect">
                        <i class="el-icon-document"></i>
                        <span slot="title">已收藏的简历</span>
                    </el-menu-item>
                    <el-menu-item index="1" @click="goPersonalInfo">
                        <i class="el-icon-user-solid"></i>
                        <span slot="title">修改我的信息</span>
                    </el-menu-item>
                </el-menu>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    name: 'Sidebar',
    methods: {
        handleOpen(key, keyPath) {
            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        //跳转个人信息页面
        goPersonalInfo() {
            this.$router.push({ path: '/userInfo/personalInfo' })
        },
        //跳转收藏页面
        goCollect() {
            this.$router.push({ path: '/userInfo/collect' })
        },
        goUpload() {
            this.$router.push({ path: '/userInfo/upload' })
        }
    },
    mounted() {
        this.$store.dispatch("getUserInfo", localStorage.getItem('id'))

    },
    computed: {
        userName() {
            return this.$store.state.user.userInfo.userName
        },
        userCompany() {
            return this.$store.state.user.userInfo.userCompany
        },
        active() {
            if (this.$route.path == '/userInfo/personalInfo') {
                return '1'
            } else if (this.$route.path == '/userInfo/collect') {
                return '2'
            } else if (this.$route.path == '/userInfo/upload') {
                return '3'
            }
        },
        imgUrl() {
            return this.$store.state.user.userInfo.userImg

        }
    }
}
</script>

<style lang="scss" scoped>
.sidebar {

    .tac {
        .userImg {
            width: 40px;
            height: 40px;
            scale: 1.5;
            margin: 20px auto;

            .userName {
                color: white;
                width: 120px;
                display: inline-block;
                transform: translateX(-40px);
                text-align: center;
            }

            .userCompany {
                display: inline-block;
                color: white;
                text-align: center;
                width: 120px;
                margin-top: 10px;
                transform: translateX(-40px);
            }
        }

        width: 200px;
        height: 650px;
        background-color: rgba(27, 42, 78);

        .col {
            width: 100%;

            transform: translateY(70px);
        }
    }
}
</style>