<!-- 注册页面 -->
<template>
  <div class="reg-container">
    <div class="title">注册</div>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="电话号码" prop="userPhone">
        <el-input v-model="ruleForm.userPhone"></el-input>
      </el-form-item>
      <el-form-item label="用户名" prop="userName">
        <el-input v-model="ruleForm.userName"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="所属公司" prop="userCompany">
        <el-input type="userCompany" v-model="ruleForm.userCompany" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="checkPass">
        <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
      </el-form-item>
      <el-radio-group v-model="sex" class="radio">
        <el-radio :label="'男'">男</el-radio>
        <el-radio :label="'女'">女</el-radio>
      </el-radio-group>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')" class="button">注册</el-button>
        <el-button @click="resetForm('ruleForm')" class="button">重置</el-button>
      </el-form-item>
    </el-form>
    <div class="goLog">已有账户？<router-link to="/log" class="goLog2">立即登录</router-link></div>
    <div class="problem">
      <div>如果您有任何疑问请咨询我们</div>
      <div>微信：xxxx</div>
      <div>电话：xxxx</div>
      <div>邮箱：xxxx</div>
    </div>
  </div>
</template>

<script>
import { reqUserRegister, reqUserRegisterOut } from '@/api/index'
export default {
  name: 'Register',
  data() {
    //验证电话号码
    var validatePhone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入电话号码'));
      } else {
        callback()
      }
    };
    //验证用户名
    var validateName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入用户名'));
      } else {
        callback()
      }
    }
    //验证所属公司
    var validateCompany = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入所属公司'));
      } else {
        callback()
      }
    }
    //验证密码
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    //二次验证密码
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.ruleForm.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        password: "",
        checkPass: "",
        userPhone: "",
        userName: "",
        userCompany:''
      },
      rules: {
        password: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' }
        ],
        userPhone: [
          { validator: validatePhone, trigger: 'blur' }
        ],
        userName: [
          { validator: validateName, trigger: 'blur' }
        ],
        userCompany:[
          {validator:validateCompany,trigger:'blur'}
        ]
      },
      sex: '男'
    };
  },
  methods: {
    async Register() {
      try {
        const { userName, userPhone, password,userCompany } = this.ruleForm
        const { sex } = this
        const form={userName, userPhone, password,sex,userCompany}
        this.$store.dispatch('userRegister', {form,vm:this})
      } catch (error) {
        console.log(error);
      }
    },
    //提交表单
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.Register()
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }


}
</script>

<style lang="scss" scoped>  .reg-container {
    width: 450px;
    height: 600px;
    margin: 0 auto;
    font-size: 20px;
    text-align: center;
    border-radius: 10%;
    margin-bottom: 100px;
    overflow: hidden;

    .title {
      height: 100px;
      width: 100px;
      line-height: 100px;
      text-align: center;
      margin: 0 auto;
      margin-top: 40px;
      font-size: 25px;
      font-weight: 600;
    }

    .demo-ruleForm {
      transform: translateX(-30px);

      .button {
        transform: translateX(-15px);
      }
    }

    .goLog {
      font-size: 15px;

      .goLog2 {
        color: blue
      }
    }

    .problem {
      transform: translateX(100px);
      text-align: left;

      div {
        margin: 0;
        height: 25px;
        font-size: 15px;
        transform: translateY(30px);
      }
    }

    .radio {
      transform: translateX(35px);
      margin-bottom: 20px;
    }
  }
</style>