
import { reqUserRegister, reqUserLogin,reqGetUserInfo,reqLogout,reqUpdateInfo } from "@/api/index";
const state = {
  id: localStorage.getItem("id"),
  userInfo:{}
};
const mutations = {
  USERLOG(state, id) {
    state.id = id;
  },
  USERINFO(state,userInfo){
    state.userInfo=userInfo
  },
  CLEAR(state){
    state.id=''
    state.userInfo={}
    localStorage.removeItem('id')
  }
};
const actions = {
  //注册
  async userRegister({ commit }, { form, vm }) {
    let result = await reqUserRegister(form);
    if (result.code == 1) {
      vm.$alert("您已经注册成功", "提示", {
        confirmButtonText: "去登录",
        callback: () => {
          vm.$router.push({ path: "/log" });
        },
      });
    } else {
      vm.$alert("该手机号已注册", "提示", {
        confirmButtonText: "确定",
      });
    }
  },

  //登录
  async userLogin({ commit }, { form, vm }) {
    let result = await reqUserLogin(form);
    if (result.code == 1) {
      const id =result.data.id    
      commit("USERLOG", id);
      localStorage.setItem("id", id);
      vm.$alert("登录成功", "提示");
      vm.$router.push({ path: "/home" });
    } else {
      vm.$message("账号或密码错误");
    }
  },
  //获取用户名
  async getUserInfo({commit},id){
    if(id){
      let result=await reqGetUserInfo(id)
      if(result.code==1){
        commit("USERINFO",result.data)
        return 'ok'
      }else{
        return Promise.reject(new Error('faile'))
      }
    }
  },
  //退出
  async userLogout({commit}){
    let result=await reqLogout()
    if(result.code==1){
        commit("CLEAR")
        return 'ok'
    }else{
        return Promise.reject(new Error('falie'))
    }
},
  //更新个人信息
  async updateInfo({commit},{form,vm}){
    let result=await reqUpdateInfo(form)
    if(result.code==1){
      vm.$alert("修改成功", "提示");
      return 'ok'
    }else{
      return Promise.reject(new Error('falie'))
    }
  }
};
const getters = {};
export default {
  state,
  mutations,
  actions,
  getters,
};
