import Vue from "vue";
import VueRouter from "vue-router";

// 引入组件
import Home from "../pages/Home";
import Log from "../pages/Log";
import Register from "../pages/Register";
import userInfo from "../pages/userInfo";

// 要告诉 vue 使用 vueRouter
Vue.use(VueRouter);

const routes = [
  {
    path: "/home",
    component: Home,
  },
  {
    path: "/log",
    component: Log,
  },
  {
    path: "/register",
    component: Register,
  },
  {
    path: "/",
    redirect: "/home",
  },
  {
    path: "/analyze",
    component: () => import("@/pages/Analyze"),
  },
  {
    path: "/userInfo",
    component: userInfo,
    redirect: "/userInfo/upload",
    children: [
      {
        path: "/userInfo/personalInfo",
        component: () => import("@/pages/personalInfo"),
      },
      { path: "/userInfo/collect", component: () => import("@/pages/Collect") },
      { path: "/userInfo/upload", component: () => import("@/pages/Upload") },
    ],
  },
  {
    path: "/dataAnalyze",
    component: () => import("@/pages/dataAnalyze"),
  },
  {
    path: "/about",
    component: () => import("@/pages/About"),
    children: [
      { path: "/about/aboutUs", component: () => import("@/pages/aboutUs") },
      { path: "/about/develop", component: () => import("@/pages/Develop") },
      { path: "/about/update", component: () => import("@/pages/Update") },
    ],
  },
  {
    path: "/postMatch",
    component: () => import("@/pages/postMatch"),
  },
  {
    path: "/analyze/result/:id",
    name: "Analyze",
    component: () => import("@/pages/analyzeResult"),
  },
];

var router = new VueRouter({
  routes,
  scrollBehavior(to, from, savedPosition) {
    // 如果有savedPosition，则在切换路由时恢复之前的滚动位置
    if (savedPosition) {
      return savedPosition;
    }
    
    // 否则，滚动到页面顶部
    return { x: 0, y: 0 };
  }
});
export default router;
