import requests from "./request";

//注册接口
export const reqUserRegister=(data)=>requests({url:`/user/registry`,data,method:'post'})

//登出接口
export const reqUserRegisterOut=()=>requests({url:`/user/registryout`,method:'post'})

//登录接口
export const reqUserLogin=(data)=>requests({url:"/user/login",data,method:'post'})

//获取用户信息接口
export const reqGetUserInfo=(id)=>requests({url:`/user/getUserInfo/${id}`,method:'get'})

//上传头像接口 
export const reqUserImg=(id,data)=> {
  const formData = new FormData();
  formData.append('img', data);
  return requests({url:`/user/updateImg/${id}`,method:'put',data: formData,headers: {
      'Content-Type': 'multipart/form-data; boundary=<calculated when request is sent>'
    }})
}

//搜索框接口 
export const reqSearchCv=(id,data)=>requests({url:`/cv/findCvByWord/${id}`,method:'get',params:{word:data}})

//退出接口
export const reqLogout=()=>requests({url:`/user/logout`,method:'post'})

//更新个人信息接口
export const reqUpdateInfo=(data)=>requests({url:`/user`,method:'put',data})

//存储简历接口
export const reqSaveResume=(id,data)=> {
    const formData = new FormData();
    formData.append('cvList', data);
    return requests({url:`/cv/uploadCv/${id}`,method:'post',data: formData,headers: {
        'Content-Type': 'multipart/form-data; boundary=<calculated when request is sent>'
      }})
}

//上传人岗匹配
export const reqUploadWork=(id,data)=> {
  const formData = new FormData();
  formData.append('workList', data);
  return requests({url:`/work/uploadWork/${id}`,method:'post',data: formData,headers: {
      'Content-Type': 'multipart/form-data; boundary=<calculated when request is sent>'
    }})
}

//匹配岗位接口
export const reqMatch=(id)=>requests({url:`/work/match/${id}`,method:'get'})


//获取该用户上传的岗位接口
export const reqListWork=(id)=>requests({url:`/work/listWork/${id}`,method:'get'})

//获取该用户上传的简历接口
export const reqUserUpload=(id,number)=>requests({url:`/cv/listCv/${id}/${number}`,method:'get'})

//获取简历接口
export const reqUserResume=(id)=>requests({url:`/cv/getCv/${id}`,method:'get'})

//获取简历文本接口
export const reqReumeTxt=(id)=>requests({url:`/cv/getCvTxt/${id}`,method:'get'})

//收藏接口
export const reqCollect=(id)=>requests({url:`/cv/collectCv/${id}`,method:'put'})

//取消收藏接口
export const reqUnCollect=(id)=>requests({url:`/cv/unCollectCv/${id}`,method:'put'})

//获取收藏简历接口
export const reqCollectResume=(id,page)=>requests({url:`/user/listCollectCv/${id}/${page}`,method:'get'})


//删除简历接口
export const reqDelResume=(id)=>requests({url:`/cv/deleteCv/${id}`,method:'DELETE'})


//标签筛选接口
export const reqSearch=(id,data,page)=>requests({url:`/cv/findCvByTags/${id}/${page}`,method:'post',data})

//数据可视化接口
export const reqDataVisual=(id)=>requests({url:`/cv/getCvView/${id}`,method:'get'})

